a {
  text-decoration: none;
}

#aboutMe {
  font-size: 50px;
}

body {
  background-color: rgb(39, 37, 37);
  color: lightgray;
}

header {
  padding: 15px;
  font-family: "Raleway", sans-serif;
  background-color: rgb(28, 28, 68);
}

header h1 {
  display: inline-block;
  font-size: 40px;
  font-family: "Luckiest Guy", cursive;
  color: rgb(101, 101, 134);
}

header nav {
  padding-top: 15px;
  margin-right: 15px;
  float: right;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
}

header nav ul {
  list-style-type: none;
}

header nav ul li {
  display: inline-block;
  margin-left: 25px;
}

h2 {
  font-size: 50px;
  font-family: "Luckiest Guy", cursive;
  padding-left: 4%;
}

.float-left {
  float: left;
  margin-left: 25px;
  height: 350px;
  width: 19%;
  padding: 10px;
  padding-right: 35px;
  flex-wrap: wrap;
}

#explore-mars,
#planner,
#horiseon,
#community-sidekicks,
#read-me {
  width: 350px;
  height: 300px;
}

#contact-icon {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

div {
  column-gap: 10px;
}

p,
.col {
  font-size: 25px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 60px;
  font-family: "Raleway", sans-serif;
  margin-top: 0;
  margin-bottom: 1rem;
  display: block;
  margin-block-start: 5px;
  margin-block-end: 5px;
}

footer {
  padding: 30px;
  clear: both;
  font-family: "Raleway", sans-serif;
  text-align: center;
  font-size: 5px;
}
